@import url(https://fonts.googleapis.com/css?family=Abel);
body {
  margin: 0;
  height: 100vh;
  font-family: 'Abel', sans-serif;
}

#root {
  height: 100%;
}
.App {
  height: 100vh;
  background-size: cover;
  color: white;
  overflow-y: hidden;
  display: -webkit-flex;
  display: flex;
}

nav {
  padding: 10px;
}

.page {
  padding: 10px;
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  overflow-y: hidden;
}

a {
  text-decoration: none;
  color: black;
}


.navigation {
  background-color: rgb(53, 50, 50)
}


.home_page {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.question {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.question > svg:hover {
  cursor: pointer;
}

.question > svg {
  position: relative;
  -webkit-animation: shine 2s infinite ease-in-out;
          animation: shine 2s infinite ease-in-out;
}

@-webkit-keyframes shine {
  0%   {fill: rgb(252, 199, 199);}
  25%  {fill: rgb(233, 110, 94);}
  50%  {fill: rgb(207, 54, 37);}
  100% {fill: rgb(252, 199, 199);}
}

@keyframes shine {
  0%   {fill: rgb(252, 199, 199);}
  25%  {fill: rgb(233, 110, 94);}
  50%  {fill: rgb(207, 54, 37);}
  100% {fill: rgb(252, 199, 199);}
}

.picture_detail {
  visibility: hidden;
  width: 50%;
  padding: 10px;
  background-color: rgb(106, 104, 109, 0.5);
  text-align: left;
  position: absolute;
  bottom: 15%;
}

.picture_detail.show {
  visibility: visible;
}
.search {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  padding: 5px;
  margin-bottom: 20px;
}

.search_btn:hover {
  cursor: pointer;
}

.extra_padding {
  width: 25%;
  padding: 10px;
  border-radius: 10px;
  border: none;
}

.result_page {
  -webkit-flex: 1 1;
          flex: 1 1;
  overflow-y: scroll;
}

.images {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 5px;
  border-radius: 10px;
  background-color: rgba(26, 21, 21, 0.3)
}

.line {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 50%;
  height: 47px;
  border-bottom: 1px solid white;
  position: relative;
  }

.card {
  width: 80%;
  margin: 0 auto;
  margin-bottom: 20px;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr;
  background-color: transparent;
}

.card-item {
  border: 1px solid white;
  padding: 10px;
  background-color: rgba(26, 26, 31, 0.5);
}

/* I've learnt how to do 3d earth from this website https://w3bits.com/css-earth/ */

#earth {
  /* Allow it to contain the absolutely positions pseudo-elements (later-on) */
  position: relative;

  width: 100px;
  height: 100px;

  transition: -webkit-transform 200ms linear;

  transition: transform 200ms linear;

  transition: transform 200ms linear, -webkit-transform 200ms linear;
  -webkit-animation: rotate 20s linear infinite;
          animation: rotate 20s linear infinite;

  /* Tweak the appearance, and give it a nice background i.e. the world map */
  color: #000;
  border-radius: 50%;
  background: url("%PUBLIC_URL%/images.jpeg") 0 0 repeat / 130px;
  box-shadow: inset -10px 0px 30px 0px rgb(78, 76, 76);

  /* Position things in a 3d space */
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

@-webkit-keyframes rotate {
  0% {background-position: 0 0;}
  100% {background-position: 630px 0;}
}

@keyframes rotate {
  0% {background-position: 0 0;}
  100% {background-position: 630px 0;}
}

#earth:before {
  /* Again, break the flow to show this as an overlay */
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;


  /* Add some shape and overlay effect to it */
  opacity: .2;
  border-radius: 50%;
  background: radial-gradient(circle at 100px 100px, #fff, #000);
}

#earth:after {
  /* Break the flow to show this as an overlay */
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  /* Give it some shape and shadow */
  border-radius: 50%;
  box-shadow: -20px 0 15px -9px rgba(0,0,0,.9) inset
}

#asteroid {
  height: 80px;
  width: 80px;
  background-size: 100%;
  -webkit-animation: rotation 2s infinite linear;
          animation: rotation 2s infinite linear;
}

@-webkit-keyframes rotation {
  from {
      -webkit-transform: rotate(0deg);
  }
  to {
      -webkit-transform: rotate(359deg);
  }
}

@keyframes rotation {
  from {
      -webkit-transform: rotate(0deg);
  }
  to {
      -webkit-transform: rotate(359deg);
  }
}
.mars_page {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.display_pictures {
  width: 50%;
